import React from "react";

export default function CartItem({ item, value }) {
  const { id, title, img, price, total, count } = item;
  const { increment, decrement, removeItem } = value;
  return (
    <div className="row my-10 text-capitalize text-center">
      <div className="col-10 mx-auto col-lg-2">
        <img
          src={img}
          style={{ width: "5rem", height: "5rem" }}
          className="img-fluid"
          alt=""
        />
      </div>

      <div className="col-10 mx-auto my-auto col-lg-2">
        <span className="d-lg-none">product : </span>
        {title}
      </div>

      <div className="col-10 mx-auto my-auto col-lg-2">
        <strong>
          <span className="d-lg-none">price : </span>
          {price}
        </strong>
      </div>

      <div className="col-10 mx-auto  col-lg-2  my-auto">
        <div className="d-flex justify-content-center">
          <div >
            <span
              className="span btn btn-black mx-1 "
              onClick={() => decrement(id)}
            >
              -
            </span>

            <span className="span btn btn-black mx-1 ">{count}</span>

            <span
              className="span btn btn-black mx-1 "
              onClick={() => increment(id)}
            >
              +
            </span>
          </div>
        </div>
      </div>

      <div className="col-10 mx-auto my-auto col-lg-2">
        <div
          className="cart-icon"
          onClick={() => {
            removeItem(id);
          }}
        >
          <i className="icon ion-ios-close" />
        </div>
      </div>

      <div className="col-10 mx-auto my-auto col-lg-2">
        <strong>item total: ${total}</strong>
      </div>
    </div>
  );
}
